<template>
  <div id="mobileUserAdd">
    <Modal
      v-model="mobileUserBuild"
      title=""
      @on-cancel="reset"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="name" label="">
          <Input v-model.trim="AddData.name" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="serviceUrl" label="">
          <Input v-model.trim="AddData.serviceUrl" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="remark" label="">
          <Input v-model.trim="AddData.remark" placeholder="" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="loading1" @click="addSubmit"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { addShp } from '@/api/adjustment'
export default {
  name: 'mobileUserAdd',
  data () {
    return {
      /*  */
      AddData: {
        serviceUrl: '',
        name: '',
        remark: ''
      },
      AddRule: {
        serviceUrl: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      /*  */
      mobileUserBuild: false,
      /*  */
      loading1: false
    }
  },
  props: {},
  methods: {
    /* axios:  */
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          this.loading1 = true
          addShp({
            data: {
              serviceUrl: this.AddData.serviceUrl,
              name: this.AddData.name,
              remark: this.AddData.remark
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.loading1 = false
              this.$emit('getNewList')
              this.$Notice.success({ title: '' })
              this.mobileUserBuild = false
              this.AddData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          }).catch(() => {
            this.$Notice.error({ title: '' })
          })
        }
      })
    },
    // 
    reset () {
      this.AddData = {
        serviceUrl: '',
        name: '',
        remark: ''
      }
      this.$refs.add.resetFields()
      this.loading1 = false
    }
  }
}
</script>

<style scoped lang="scss">
</style>
