import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
import { tdtlayer, tdtyNoteslayer, redLine } from '@/utils/layers'
import Basemapthumbnail1 from '@/assets/images/map_images/Basemapthumbnail1.png'
/*  */
export default {
  data () {
    return {
    }
  },
  methods: {
    /*  */
    async createMap () {
      // arcgis
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap

      // 
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })
      // 
      // let redLinelayer = await redLine()

      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      // let initExtent = new Extent(-13056650, 6077558, -13055709, 6077938, new SpatialReference({ wkid: 3857 }))
      // let initExtent = new Extent(12934703, 4888113, 12937312, 4886589, new SpatialReference({ wkid: 3857 }))

      this.map = new Map({
        // basemap: 'satellite'
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 6
      })
      this.view.ui._removeComponents(['zoom'])
    },
    /*  */
    async createReferMap () {
      // arcgis
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap

      // 
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })

      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      // let initExtent = new Extent(-13056650, 6077558, -13055709, 6077938, new SpatialReference({ wkid: 3857 }))
      // let initExtent = new Extent(12934703, 4888113, 12937312, 4886589, new SpatialReference({ wkid: 3857 }))

      this.refermap = new Map({
        // basemap: 'satellite'
        basemap: customBasemap1
      })
      this.referview = new MapView({
        map: this.refermap,
        container: 'referViewDiv',
        center: config.centerPoint,
        zoom: 6
      })
      this.referview.ui._removeComponents(['zoom'])

      let views = [this.view, this.referview]
      views.forEach((view) => {
        // 
        let fullextent = null
        // 
        view.on('mouse-wheel', (e) => {
          // 
          // 
          fullextent = view.extent
          // 
          views.forEach((nview) => {
            nview.extent = fullextent
          })
        })
        // 
        view.on('drag', (e) => {
          // 
          fullextent = view.extent
          // 
          views.forEach((nview) => {
            nview.extent = fullextent
          })
        })
      })
    },
    /*  */
    async addWidgets () {
      // 
      this.view.ui.add(document.getElementById('zoomIn'), 'top-left')
      // 
      this.view.ui.add(document.getElementById('zoomOut'), 'top-left')
      // 
      this.referview.ui.add(document.getElementById('referZoomIn'), 'top-left')
      // 
      this.referview.ui.add(document.getElementById('referZoomOut'), 'top-left')
      // 
      let Fullscreen = await arcgisPackage.Fullscreen
      let fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.ui.add(fullscreen, 'top-left')
    },
    /*  */
    zoomIn () {
      this.view.goTo({
        center: this.view.center,
        zoom: this.view.zoom + 1
      })
    },
    /*  */
    zoomOut () {
      this.view.goTo({
        center: this.view.center,
        zoom: this.view.zoom - 1
      })
    }
  }
}
