import config from '../config'
/*  */
export default {
  data () {
    return {
      attributeStatus: false,
      attributeObj: {}, // 
      attributeCopy: {}, // 
      attributeEdit: false, // 
      nameValue: '', // 
      typeValue: '', // 
      xzqhValue: '', // 
      regionValue: '', // 
      positionValue: '' // 
    }
  },
  filters: {
    nameToAlias (val) {
      for (let item of config.orchardFiled) {
        if (val === item.name) {
          return item.alias
        }
      }
    }
  },
  watch: {
    attributeObj (attr) {
      this.$nextTick(() => {
        // ,
        // this.$refs.ObjectIDRef.innerText = attr['ObjectID'] || ''
        this.$refs.puctRef.innerText = attr['name'] || ''
        this.$refs.xzqhRef.innerText = this.xzqhtreeNameMap[attr['xzqh']] || ''
        this.$refs.typeRef.innerText = this.treeNameMap[attr['type']] || ''
        this.$refs.regionRef.innerText = attr['region'] || ''
        this.$refs.positionRef.innerText = attr['position'] || ''
        this.$refs.areaRef.innerText = attr['area'] || ''
        this.$refs.centerRef.innerText = attr['center'] || ''
        this.attributeEdit = false
      })
    }
  },
  methods: {
    /*  */
    attributeStateToggle () {
      /*  */
      this.closeOtherTools('attribute')
      if (!this.attributeStatus) {
        this.attributeStatus = true
        this.attributeObj = {}
      } else {
        this.map.findLayerById('sLayer').removeAll()
        this.attributeStatus = false
      }
    },
    /*  */
    attrDelchoice (id) {
      this.map.findLayerById('sLayer').removeAll()
      this.originalSelectedGraphics = this.originalSelectedGraphics.filter((res) => res.attributes.ObjectID !== id)
      this.map.findLayerById('sLayer').addMany(this.originalSelectedGraphics)
    },
    /*  */
    attrChickList (attr) {
      console.log(this.originalSelectedGraphics)
      /* if (!isNaN(attr.area)) {
        attr.area = attr.area + ''
      } */
      // 
      this.attributeObj = attr
      // 
      let gras = []
      for (let item of this.originalSelectedGraphics) {
        let gra = item.clone()
        if (item.attributes.ObjectID === attr.ObjectID) {
          gra.symbol = config.attributeSym
        }
        gras.push(gra)
      }
      this.map.findLayerById('sLayer').removeAll()
      this.map.findLayerById('sLayer').addMany(gras)
    },
    /*  */
    attrCopy (attr) {
      if (JSON.stringify(this.attributeCopy) === '{}') {
        this.attributeCopy = attr
      } else {
        this.attributeCopy = {}
      }
    },
    /*  */
    async attrPaste (id) {
      if (JSON.stringify(this.attributeCopy) !== '{}') {
        // 
        let arrGras = []
        for (let item of this.originalSelectedGraphics) {
          let gra = item.clone()
          if (item.attributes.ObjectID === id) {
            let attr = JSON.parse(JSON.stringify(this.attributeCopy))
            for (let i in attr) {
              if (i !== 'ObjectID') {
                gra.attributes[i] = attr[i]
              }
            }
          }
          arrGras.push(gra)
        }
        // 
        this.map.findLayerById('sLayer').removeAll()
        this.map.findLayerById('sLayer').addMany(arrGras)
        // 
        this.map.findLayerById('orchardLayer').applyEdits({
          updateFeatures: arrGras
        })
        // 
        this.originalSelectedGraphics = arrGras
        // 
        this.attrChickList(this.attributeObj)
        // 
        this.attributeCopy = {}
      }
    },
    /*  */
    attrCover () {
      if (JSON.stringify(this.attributeCopy) !== '{}') {
        // 
        let arrGras = []
        for (let item of this.originalSelectedGraphics) {
          let gra = item.clone()
          let attr = JSON.parse(JSON.stringify(this.attributeCopy))
          for (let i in attr) {
            if (i !== 'ObjectID') {
              gra.attributes[i] = attr[i]
            }
          }
          arrGras.push(gra)
        }
        // 
        this.map.findLayerById('sLayer').removeAll()
        this.map.findLayerById('sLayer').addMany(arrGras)
        // 
        this.map.findLayerById('orchardLayer').applyEdits({
          updateFeatures: arrGras
        })
        // 
        this.originalSelectedGraphics = arrGras
        // 
        this.attrChickList(this.attributeObj)
        // 
        this.attributeCopy = {}
      }
    },
    /*  */
    attrEdit () {
      this.typeValue = this.$refs.typeRef.innerText
      console.log(this.treeNameMap)
      for (let key in this.treeNameMap) {
        if (this.treeNameMap[key] === this.$refs.typeRef.innerText) {
          this.typeValue = this.typeMap[key]
        }
      }
      console.log(this.typeValue)
      this.xzqhValue = this.$refs.xzqhRef.innerText
      console.log(this.xzqhtreeNameMap)
      console.log(this.$refs.xzqhRef.innerText)
      for (let key in this.xzqhtreeNameMap) {
        if (this.xzqhtreeNameMap[key] === this.$refs.xzqhRef.innerText) {
          this.xzqhValue = this.xzqhMap[key]
        }
      }
      if (this.$refs.xzqhRef.innerText === '') {
        this.xzqhValue = []
      }
      console.log(this.xzqhValue)
      this.regionValue = this.$refs.regionRef.innerText
      this.positionValue = this.$refs.positionRef.innerText
      this.nameValue = this.$refs.puctRef.innerText
      this.attributeEdit = true
    },
    /*  */
    attrConserve () {
      // ref
      console.log(this.$refs.attrKey)
      let obj = {
        'puct': this.$refs.puctRef.innerText,
        'xzqh': this.xzqhValue[this.xzqhValue.length - 1],
        'type': this.typeValue[this.typeValue.length - 1],
        'name': this.nameValue,
        'region': this.regionValue,
        'position': this.positionValue,
        'area': this.$refs.areaRef.innerText,
        'center': this.$refs.centerRef.innerText
      }
      // 
      let arrGras = []
      for (let item of this.originalSelectedGraphics) {
        let gra = item.clone()
        if (item.attributes.ObjectID === this.attributeObj.ObjectID) {
          for (let i in obj) {
            if (i !== 'ObjectID') {
              this.attributeObj[i] = gra.attributes[i] = obj[i]
            }
          }
        }
        arrGras.push(gra)
      }
      // 
      this.map.findLayerById('sLayer').removeAll()
      this.map.findLayerById('sLayer').addMany(arrGras)
      // 
      this.map.findLayerById('orchardLayer').applyEdits({
        updateFeatures: arrGras
      })
      // 
      this.originalSelectedGraphics = arrGras
      // 
      this.attrChickList(this.attributeObj)
      // 
      this.attributeEdit = false
      this.$nextTick(() => {
        this.$refs.typeRef.innerText = this.typeValue
        this.$refs.regionRef.innerText = this.regionValue
        this.$refs.positionRef.innerText = this.positionValue
        this.$refs.puctRef.innerText = this.nameValue
        this.$refs.typeRef.innerText = this.treeNameMap[this.typeValue[this.typeValue.length - 1]] || ''
        this.$refs.xzqhRef.innerText = this.xzqhtreeNameMap[this.xzqhValue[this.xzqhValue.length - 1]] || ''
      })

    }
  }
}
