import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/*  */
export default {
  data () {
    return {
      scatterStatus: false
    }
  },
  methods: {
    /*  */
    scatterClick () {
      /*  */
      this.closeOtherTools()
      let sLayer = this.map.findLayerById('sLayer')
      if (!sLayer.graphics.length) {
        setTimeout(() => {
          this.$Modal.warning({
            title: '',
            content: ''
          })
          // 
          this.activeChoice()
        }, 300)
      } else if (sLayer.graphics.length > 1) {
        setTimeout(() => {
          this.$Modal.error({
            title: '',
            content: ''
          })
        }, 300)
      } else {
        this.scatterGeos()
      }
    },
    async scatterGeos () {
      let Graphic = await arcgisPackage.Graphic
      let sLayer = this.map.findLayerById('sLayer')
      let graphicArr = []
      for (let item of sLayer.graphics.items[0].geometry.rings) {
        let graphic = new Graphic()
        graphic.symbol = config.selectionSym
        graphic.geometry = {
          type: sLayer.graphics.items[0].geometry.type,
          rings: item,
          spatialReference: config.spatialReference
        }
        graphic.attributes = JSON.parse(JSON.stringify(sLayer.graphics.items[0].attributes))
        graphicArr.push(graphic)
      }
      // 
      for (let i in graphicArr) {
        await this.getAreaCenter(graphicArr[i])
      }
      /*  */
      this.map.findLayerById('orchardLayer').applyEdits({
        addFeatures: graphicArr,
        deleteFeatures: this.originalSelectedGraphics
      })
      /*  */
      this.map.findLayerById('sLayer').removeAll()
      // 
      this.activeChoice()
    }
  }
}
