import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
// import { getBaseYearDataByUrl } from '../../../api/collection'
/*  */
export default {
  data () {
    return {
      referPanelStatus: false, // 
      referbackSocial: ['orchardLayer', 'backLayer', 'examLayer'], // id
      referbackListStatus: false, // 
      referselectBackObj: {}, // 
      referselectBackId: '', // ID,
      referorchardToolsStatus: true, // 
      referbackToolsStatus: false, // 
      referexamToolsStatus: true // 
    }
  },
  watch: {
    /*  */
    referselectBackId (val) {
      for (let item of this.backList) {
        if (item.ab001 === val) {
          this.referselectBackObj = item
        }
      }
      console.log(this.referselectBackObj)
      if (!this.referselectBackObj.ab010) {
        this.getBackDataRefer()
      } else {
        this.getBackDataRefer(this.referselectBackObj.ab010)
      }
    }
  },
  methods: {
    /*  */
    async getBackDataRefer (url) {
      let features = []
      /* if (url) {
        await getBaseYearDataByUrl({
          url: url
        }).then((res) => {
          if (res.code === 1) {
            features = res.data
            for (let i in features) {
              features[i].geometry.rings = JSON.parse(features[i].geometry.rings)
              features[i].geometry.spatialReference = config.spatialReference
            }
          } else {
            this.$Notice.error({ title: res.msg })
          }
        })
      } */
      this.refermap.findLayerById('backLayer').queryFeatures().then((res) => {
        this.refermap.findLayerById('backLayer').applyEdits({
          deleteFeatures: res.features
        })
        this.refermap.findLayerById('backLayer').applyEdits({
          addFeatures: features
        })
      })
    },
    /*  */
    backSocialChangeRefer (backS) {
      // 
      this.refermap.findLayerById('backLayer').visible = false
      console.log(backS)
      // 
      if (backS.indexOf('backLayer') !== -1) {
        this.refermap.findLayerById('backLayer').visible = true
      }
    }
  }
}
