import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
// import { getBaseYearDataByUrl } from '../../../api/collection'
// import { getAbList } from '../../../api/manage'
/*  */
export default {
  data () {
    return {
      layerPanelStatus: false, // 
      backList: [], // 
      configSocial: ['orchardLayer', 'backLayer', 'examLayer'], // id
      backSocial: ['orchardLayer', 'backLayer', 'examLayer'], // id
      backListStatus: false, // 
      selectBackObj: {}, // 
      selectBackId: '', // id,
      orchardToolsStatus: true, // 
      backToolsStatus: false, // 
      examToolsStatus: true // 
    }
  },
  watch: {
    /*  */
    selectBackId (val) {
      for (let item of this.backList) {
        if (item.ab001 === val) {
          this.selectBackObj = item
        }
      }
      if (!this.selectBackObj.ab010) {
        this.getBackData()
      } else {
        this.getBackData(this.selectBackObj.ab010)
      }
    }
  },
  methods: {
    /* 5 */
    getBackList () {
      /* getAbList({
        pageNum: 1,
        pageSize: 5
      }).then((res) => {
        if (res.code === 1) {
          this.backList = res.data.rows
          /!*  *!/
          this.$nextTick(() => {
            if (this.backList.length > 0) {
              this.referselectBackId = this.backList[0].ab001
            }
          })
        } else {
          this.$Notice.error({ title: res.msg })
        }
      }) */
    },
    /*  */
    async getBackData (url) {
      console.log(url)
      let features = []
      /* if (url) {
        await getBaseYearDataByUrl({
          url: url
        }).then((res) => {
          if (res.code === 1) {
            console.log(res)
            features = res.data
            for (let i in features) {
              features[i].geometry.rings = JSON.parse(features[i].geometry.rings)
              features[i].geometry.spatialReference = config.spatialReference
            }
          } else {
            this.$Notice.error({ title: res.msg })
          }
        })
      } */
      this.map.findLayerById('backLayer').queryFeatures().then((res) => {
        this.map.findLayerById('backLayer').applyEdits({
          deleteFeatures: res.features
        })
        this.map.findLayerById('backLayer').applyEdits({
          addFeatures: features
        })
      })
    },
    /*  */
    backSocialChange (backS) {
      // 
      for (let item of this.configSocial) {
        if (item === 'examLayer') {
          this.map.findLayerById('examLayer').visible = false
          this.map.findLayerById('examChangeLayer').visible = false
        } else {
          this.map.findLayerById(item).visible = false
        }
      }
      // 
      for (let item of backS) {
        if (item === 'examLayer') {
          this.map.findLayerById('examLayer').visible = true
          this.map.findLayerById('examChangeLayer').visible = true
        } else {
          this.map.findLayerById(item).visible = true
        }
      }
      // 
      this.map.findLayerById('sLayer').removeAll()
      // 
      this.toolsControl(backS)
    },
    /*  */
    toolsControl (backS) {
      //     
      if (backS.indexOf('orchardLayer') !== -1) {
        this.orchardToolsStatus = true
        this.backToolsStatus = false
      }
      //     
      if (backS.indexOf('orchardLayer') === -1) {
        if (backS.indexOf('backLayer') !== -1) {
          this.orchardToolsStatus = false
          this.backToolsStatus = true
          // 
          this.choiceLayerId = 'backLayer'
        } else {
          this.orchardToolsStatus = false
          this.backToolsStatus = false
          // 
          this.choiceLayerId = 'orchardLayer'
        }
      }
      //    
      if (backS.indexOf('examLayer') !== -1) {
        this.examToolsStatus = true
      } else {
        this.examToolsStatus = false
      }
    }
  }
}
