/*  */
export default {
  data () {
    return {
    }
  },
  methods: {
    async copyClick () {
      let sLayer = this.refermap.findLayerById('sLayer')
      if (!sLayer.graphics.length) {
        this.$Modal.warning({
          title: '',
          content: ''
        })
        // 
        this.activeChoice()
      } else {
        this.$Modal.confirm({
          title: '',
          content: '<p></p>',
          onOk: () => {
            console.log(this.referOriginalSelectedGraphics)
            this.map.findLayerById('orchardLayer').applyEdits({
              addFeatures: this.referOriginalSelectedGraphics
            })
            this.openOrchar()
          }
        })
      }
    }
  }
}
